import React from 'react';
import './index.css';
import { Layout,Image,Typography } from 'antd';
import imgSrc from '../../assets/images/dataRe.svg'

const { Content } = Layout;
const { Title,Paragraph } = Typography;
const About = () => {

    return (
        <Content className="content-about">
            <div className="image">
                <Image src={imgSrc} width={600} fallback="data:image/png;base64," />
            </div>
            <div className="introduce">
                <Title level={2}>介绍</Title>
                <Paragraph >
                    1987年，中国科学技术信息研究所（ISTIC）受科技部（原国家科委）的委托，开始对中国科技人员在国内外发表论文的数量和被引用情况进行统计分析，并利用统计数据建立了中国科技论文与引文数据库（CSTPCD）。这项工作开展后受到了社会各界的普遍重视和广泛好评，多年以来积累了大量的宝贵数据，为科技部等各级管理部门、高等院校、科研机构、期刊编辑部和科研工作者提供了各类论文统计基础数据和期刊评估指标。
                </Paragraph>
                <Paragraph >
                    《中国科技期刊引证报告》（CJCR）的研制出版始于1997年，是一种专门用于期刊引用分析研究的重要检索评价工具。利用CJCR所提供的统计数据，可以清楚地了解期刊引用和被引用的情况，以及进行引用效率、引用网络、期刊自引等统计分析。同时，利用CJCR中的期刊评价指标，还可以方便地定量评价期刊的相互影响和相互作用，正确评估某种期刊在科学交流体系中的作用和地位。自CJCR问世以来，在开展科研管理和科学评价期刊方面一直发挥着巨大的作用。
                </Paragraph>
                <Paragraph >
                    每年《中国科技期刊引证报告》均有新的变化和进步。我们衷心希望CJCR能成为广大读者开展工作时检索查询的友好助手和得力工具，并愿为大家奉献一份独一无二的科技期刊分析与评价报告。
                </Paragraph>
            </div>

        </Content>
    );

}

export default About;