import React from 'react';
import { Table } from 'antd';
import { AppContext } from '../../context';

const { useContext } = React;
const { Column,ColumnGroup } = Table;

const JournalList = ()=>{
  const { subjectInfo } = useContext(AppContext);
  
  const isEmpty = subjectInfo.length ===0;
  if(isEmpty) return <span/>;

  const journalList = subjectInfo[0].journallist.map((d,index)=>({key:index,...d}));
  const subjectName = subjectInfo[0].subject.name;
  
  return (
    <Table dataSource ={journalList} bordered size="middle" title={()=>subjectName+" | 学科期刊主要指标"}>
      <Column title='刊名' dataIndex="name" key="name" sorter={(a,b)=>a.name.length-b.name.length} render={(text,record)=><a href={"/journal/"+record.journal_id}>{record.name}</a>}></Column>
      <ColumnGroup title ='核心总被引频次'>
        <Column title='数值' dataIndex="frequency" key="frequency" sorter={(a,b)=>a.frequency-b.frequency}></Column>
        <Column title='排名' dataIndex="frequency_rank" key="frequency_rank" sorter={(a,b)=>a.frequency_rank-b.frequency_rank}></Column>
        <Column title='离均差率' dataIndex="frequency_dr" key="frequency_dr" sorter={(a,b)=>a.frequency_dr-b.frequency_dr}></Column>
      </ColumnGroup>
      <ColumnGroup title ='核心影响因子'>
        <Column title='数值' dataIndex="factor" key="factor" sorter={(a,b)=>a.factor-b.factor}></Column>
        <Column title='排名' dataIndex="factor_rank" key="factor_rank" sorter={(a,b)=>a.factor_rank-b.factor_rank}></Column>
        <Column title='离均差率' dataIndex="factor_dr" key="factor_dr" sorter={(a,b)=>a.factor_dr-b.factor_dr}></Column>
      </ColumnGroup>
      <ColumnGroup title ='综合评价总分'>
        <Column title='数值' dataIndex="score" key="score" sorter={(a,b)=>a.score-b.score}></Column>
        <Column title='排名' dataIndex="score_rank" key="score_rank" sorter={(a,b)=>a.score_rank-b.score_rank}></Column>
      </ColumnGroup>
      <Column title='学科扩散指标' dataIndex="spread" key="spread" sorter={(a,b)=>a.spread-b.spread}></Column>
      <Column title='学科影响指标' dataIndex="influence" key="influence" sorter={(a,b)=>a.influence-b.influence}></Column>
      <Column title='红点指标' dataIndex="red_dot" key="red_dot" sorter={(a,b)=>a.red_dot-b.red_dot}></Column>
    </Table>
  )
}

export default JournalList