import React from 'react';
import { AppContext } from '../../context';
import WordCloudGraph from '../WordCloudGraph';

const { useContext } = React;


const RedDot = () => {
  const { subjectInfo } = useContext(AppContext);
  const isEmpty = subjectInfo.length ===0;
  if(isEmpty) return <span/>

  const keywordArr = subjectInfo[0].keyword.map((d,index)=>({
    word:d.keyword,
    weight:d.frequency,
    id:index
  }));
 
  return (
    <div>
     <div className="wordtitle">学科红点关键词</div>
      <WordCloudGraph data = {keywordArr} />      
    </div>
      
  )
}

export default RedDot;