import React from 'react'
import Loading from '../Loading';

export const RenderLoadingJSX = (WrapperedComponent,isLoading)=>{
  return isLoading ? <Loading tip="加载中..." /> : WrapperedComponent;
}

export const RenderLoadingComponent =  (WrapperedComponent,isLoading,isEmpty,LoadingHeight = '300px')=>{

  return isLoading ?  <Loading height={LoadingHeight} tip="加载中..." /> :  WrapperedComponent;
}