import React from 'react';
import { Descriptions } from 'antd';

import { AppContext } from '../../context';


const { useContext } = React;
const { Item } = Descriptions;

const JournalIndex = ()=>{
  const { journalInfo } = useContext(AppContext);
  const isEmpty = journalInfo.length ===0;
  if(isEmpty) return <span/>
  const indexArr = journalInfo[0].index;
  const descriptions = indexArr.map(d=><Item key={d.param_name} label={d.param_name}>{d.value}</Item>)
  return (
    <Descriptions bordered size="middle" column={4}>
      {descriptions}
    </Descriptions>
  )
}

export default JournalIndex