import React from 'react';
import { Radar } from '@ant-design/charts';


const RadarChart = ({ data,title }) => {
  
  const config = {
    title: {
      visible: true,
      text: title,
      alignTo: 'middle'
    },
    data,
    meta:{
      score:{
        alias:'相对分数'
      }
    },
    angleField: 'item',
    radiusField: 'score',
    radiusAxis: {
      grid: {
        alternateColor: ['rgba(0, 0, 0, 0.04)',null],
      },
    },
    area: { visible: true },
    point: { visible: true },
  };

  return <Radar {...config} />;

}

export default RadarChart;