import React from 'react';
import { Layout } from 'antd';
import './index.css';
import { getSubjectById } from '../../utils/request';
import { useParams,useHistory } from 'react-router-dom';
import ProfileSubject from '../../components/ProfileSubject';
import JournalList from '../../components/JournalList';
import RedDot from '../../components/RedDot';
import { RenderLoadingComponent } from '../../components/HOC';
import { AppContext } from '../../context';

const { Content } = Layout;
const { useState,useEffect,useContext } = React;

const Subject = () => {
  const { sid } = useParams();
  const history = useHistory();
  const [ isLoading,setIsLoading ] = useState(false);
  const { changeSubjectInfo } = useContext(AppContext);
  useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      const response = await getSubjectById(sid);
      setIsLoading(false);
      const subjectInfo = response.data.resultlist;
      if (subjectInfo[0].subject === null) {
        history.push("/404")
      }
      else {
        changeSubjectInfo(response.data.resultlist);
      }
      
    }
    fetchData();
  },[sid,changeSubjectInfo,history]);

  return (
    <Content className="content" >
      <div className="journalComponent subjectProfile">
        {RenderLoadingComponent(<ProfileSubject />,isLoading)}
      </div>
      <div className="journalComponent journalList">
        {RenderLoadingComponent(<JournalList />,isLoading)}
      </div>
      <div className="journalComponent redDot">
        {RenderLoadingComponent(<RedDot />,isLoading)}
      </div>
    </Content>
  );

}

export default Subject