import React from 'react';
import { PageHeader,Statistic,Row,Col,Card} from 'antd';

import { AppContext } from '../../context';

const { useContext } = React;

const ProfileSubject = ()=>{
  const { subjectInfo } = useContext(AppContext);
  const isEmpty = subjectInfo.length ===0;
  if(isEmpty) return <span/>
  
  const subjectName = subjectInfo[0].subject.name;
  const journalList = subjectInfo[0].journallist;
  const avgIndexArr = subjectInfo[0].avgindex;
  const subjectIndexArr= ['期刊数','AR论文数','总被引频次','影响因子','基金论文比','平均引文数'];

  const avgIndexShowArr = avgIndexArr.filter(d=>subjectIndexArr.includes(d.param_name))
  const statsticMemo = avgIndexShowArr.map((d,index)=>{
    return(
      <Col span={4} key={index}>
        <Card>
        <Statistic  title={d.param_name+"均值"} value={d.avg_value} precision={2} />
        </Card>
      </Col>
    )
  })

  return (
    <PageHeader
        ghost={false}
        onBack={() => window.history.back()}
        title={subjectName} 
        subTitle ='学科指标统计'
      >
      <Row gutter={16} justify="space-around" align="middle">
        <Col span={4} >
          <Card>
          <Statistic  title={"期刊数"} value={journalList.length} />
          </Card>         
        </Col>
        {statsticMemo}
      </Row>
    </PageHeader> 
  )
  
}
export default ProfileSubject