import React from 'react';
import {
  Line
} from '@ant-design/charts';


const LineChart = ({
  data,
  title
}) => {
  const config = {
    title: {
      visible: true,
      text: title,
      style: {
        fill: '#1890ff',
        fontSize:14
      }
    },
    description: {
      visible: true,
      text: '指标历年变化折线图',
    },
    padding: 'auto',
    forceFit: true,
    data,
    xField: 'year',
    yField: 'value',
    yAxis: { nice: true },
    meta: {
      year: { alias: '年份' },
      value: { alias: '数值' },
    },
    label: {
      visible: true,
      type: 'point',
      style: {
        fill: '#0D0E68',
        fontSize: 12,
        fontWeight: 600,
        opacity: 0.6,
      },
    },
    point: {
      visible: true,
      size: 5,
      shape: 'diamond',
      style: {
        fill: 'white',
        stroke: '#2593fc',
        lineWidth: 2,
      },
    },
  };
  return <Line {
    ...config
  }
  />;
}

export default LineChart;