import React from 'react';
import {
  Column
} from '@ant-design/charts';


const ColumnChart = ({
  data,
  title
}) => {
  const config = {
    title: {
      visible: true,
      text: title,
      style: {
        fill: '#1890ff',
        fontSize:14
      }
    },
    description: {
      visible: true,
      text: '指标历年变化柱状图',
    },
    forceFit: true,
    data,
    padding: 'auto',
    xField: 'year',
    yField: 'value',
    columnSize:32,
    meta: {
      year: { alias: '年份' },
      value: { alias: '数量' },
    },
    label: {
      visible: true,
      style: {
        fill: '#0D0E68',
        fontSize: 12,
        fontWeight: 600,
        opacity: 0.6,
      },
    },
  };
  return <Column {...config} />;
}

export default ColumnChart;