import React,{ useState,useContext } from 'react';
import { Layout,Input,AutoComplete,Button } from 'antd';
import { useHistory } from 'react-router-dom';
import { PLACEHOLDER } from '../../constants';
import './index.css';
import { getJournalListByCondition } from '../../utils/request';
import { AppContext } from '../../context'

const { Content } = Layout;
const { Group } = Input;


const Home = () => {
    const [value,setValue] = useState('');
    const [journalList,setJournalList] = useState([]);
    const [options,setOptions] = useState([]);
    const {changeJournalData} = useContext(AppContext);
    
    const history = useHistory();
    
    const onSearch = searchText => {       
        getJournalListByCondition(searchText,allJournals => {
            if (!allJournals) return;
            let listJour = allJournals && allJournals[0].result;
            setJournalList(listJour)
            let journalNameList = listJour.map(d =>({value:d.jname}));
            setOptions(
                !searchText ? [] :journalNameList
            );      
        });
    };

    const onSelect = data => {
        console.log('onSelect',data);
    };

    const onChange = data => {
        setValue(data);
    };

    const onSubmit = ()=>{     
        let journal = journalList.filter(d=>d.jname ===value)[0];
        if(!journal) return;
        changeJournalData(journal); 
        history.push('/journal/'+journal.journal_id);
    }

    return (
        <Content className="content-body">
            <div className="title">
                <h1>中国科技核心期刊引证报告</h1>
                <p>Chinese Science and Technology Journal Citation Reports</p>
            </div>
            <div className="search">
                <Group compact>
                    <AutoComplete size="large"
                        value={value}
                        options={options}
                        style={{ width: '50vw',textAlign: 'left' }}
                        placeholder= { PLACEHOLDER }
                        onSelect={onSelect}
                        onSearch={onSearch}
                        onChange={onChange}>
                    </AutoComplete>
                    <Button type="primary" size="large" onClick={onSubmit}>
                        Search
                    </Button>
                </Group>
            </div>
        </Content>
    );

}

export default Home;