import React,{useState,createContext} from 'react';

export const AppContext = createContext();

const AppProvider = ({children})=>{
    const changeLoading = isLoading => {
        changeAppState(prevState => {
          return {
            ...prevState,
            isLoading  
          }
        })
    };

    const changeJournalData = journalData =>{
        changeAppState(prevState=>{
            return {
                ...prevState,
                journalData
            }
        })
    };

    const changeJournalInfo = journalInfo=>{
        changeAppState(prevState=>{
            return {
                ...prevState,
                journalInfo
            }
        })
    };

    const changeSubjectInfo = subjectInfo=>{
        changeAppState(prevState=>{
            return {
                ...prevState,
                subjectInfo
            }
        })
    };

    const initAppState = {
        journalData:{},
        journalInfo:[],
        subjectInfo:[],
        isLoading:false,
        changeJournalData,
        changeJournalInfo,
        changeSubjectInfo,
        changeLoading
    };
    const [appState,changeAppState] = useState(initAppState);
    

    return (
        <AppContext.Provider value ={appState}>
            {children}
        </AppContext.Provider>
    )
}
export default AppProvider;