import React from 'react';
import { Route,Switch } from 'react-router-dom';
import Home from "./containers/Home"
import Journal from "./containers/Journal"
import Subject from "./containers/Subject"
import About from "./containers/About"
import NotFound from "./containers/NotFound"

function Routes() {

    return (
        <Switch>
            <Route path="/journal/:id" component={Journal} exact></Route>
            <Route path="/subject/:sid" component={Subject} exact></Route>
            <Route path="/help" component={About} exact></Route>
            <Route path="/" component={Home} exact></Route>
            <Route path="/404" component={NotFound} exact></Route>
            <Route component={NotFound} exact></Route>
        </Switch>
    );
}

export default Routes
