import React from 'react';
import { Layout,Divider } from 'antd';
import './index.css';
import { getJournalById } from '../../utils/request';
import { useParams,useHistory } from 'react-router-dom';
import ProfileJournal from '../../components/ProfileJournal';
import JournalIndex from '../../components/JournalIndex';
import SubjectIndex from '../../components/SubjectIndex';
import ChartGraph from '../../components/ChartGraph';
import { RenderLoadingComponent } from '../../components/HOC';
import { AppContext } from '../../context';
const { Content } = Layout;

const { useState,useEffect,useContext } = React;

const Journal = () => {
  const { id } = useParams();
  const history  = useHistory();
  const [ isLoading,setIsLoading ] = useState(false);
  const { changeJournalInfo } = useContext(AppContext);

  useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      const response = await getJournalById(id);
      setIsLoading(false); 
      const journalInfo = response.data.resultlist;
      if(journalInfo[0].journal === null){
        history.push("/404")
      }
      else{
        changeJournalInfo(response.data.resultlist);       
      } 
         
    }
    fetchData();
    
  },[id,changeJournalInfo,history]);

  return (
    <Content className="content" >
      <div className="journalComponent journalProfile">
        {RenderLoadingComponent(<ProfileJournal />,isLoading)}
      </div>
      <div className="journalComponent journalIndex">
        <Divider orientation="left">来源指标与被引用指标</Divider>
        {RenderLoadingComponent(<JournalIndex />,isLoading)}
        <Divider orientation="left">学科指标</Divider>
        {RenderLoadingComponent(<SubjectIndex />,isLoading)}
      </div>
      <div className="journalComponent ChartGraph">
        {RenderLoadingComponent(<ChartGraph/>,isLoading)}
      </div>
      
    </Content>
  );

}

export default Journal