import config from '../config';
import axios from 'axios'
const {
    serverJournal,
    serverSubject,
    journalMethods,
    subjectMethods
} = config;

export function getJournalListByCondition(condition,callback){
    axios({
        method:'post',
        url:serverJournal,
        params:{
            jsonStr:{
                "method":journalMethods.getJournalByCondition,
                "requestlist": [{
                    "page":"1",
                    "condition": condition
                }]
            }
        }
    }).then(response=>{
        callback(response.data.resultlist)
    })  
}

export function getJournalById(id){
    return axios({
        method:'post',
        url:serverJournal,
        params:{
            jsonStr:{
                "method":journalMethods.getJournalById,
                "requestlist": [{
                    "journal_id":id
                }]
            }
        }
    }) 
}

export function getSubjectById(id){
    return axios({
        method:'post',
        url:serverSubject,
        params:{
            jsonStr:{
                "method":subjectMethods.getSubjectById,
                "requestlist": [{
                    "subject_id":id
                }]
            }
        }
    }) 
}