import React from 'react';
import { Tabs } from 'antd';
import { AppContext } from '../../context';
import RadarChart from '../RadarChart';
import LineChart from '../LineChart';
import ColumnChart from '../ColumnChart';

const { useContext } = React;
const { TabPane } = Tabs;

const ChartGraph = () => {
  const { journalInfo } = useContext(AppContext);
  const isEmpty = journalInfo.length ===0;
  if(isEmpty) return <span/>
  const defaultScore = {
    "citations": 0,
    "open_factor": 0,
    "influence_factor": 0,
    "paper_ratio": 0,
    "citation_rate": 0,
    "frequency":0

  }
  const subjectScore = journalInfo[0].score[0]||defaultScore;
  const dataField = {
    "citations": "引文数",
    "open_factor": "开放因子",
    "influence_factor": "核心影响因子",
    "paper_ratio": "基金论文比",
    "citation_rate": "他引率",
    "frequency": "核心总被引频次"
  }
  const radarData = Object.keys(dataField).map(d => ({
    item: dataField[d],
    score: subjectScore[d]||0
  }))

  const chartName = {
    "literature": "来源文献量",
    "paper": "AR论文数",
    "fund": "基金论文比",
    "frequency": "总被引频次",
    "factor": "影响因子"
  };

  const chartTabPanes = Object.keys(chartName).map((d,index) => {
    const dataSetArr = journalInfo[0][d];
    const data = dataSetArr.map(item => ({
      year: item.year,
      value: item.value
    }))
    return (<TabPane tab={chartName[d]} key={index}>
      {d==="literature" || d==="paper"?
      <ColumnChart data={data} title={chartName[d]}></ColumnChart>: <LineChart data={data} title={chartName[d]}></LineChart>}

    </TabPane>)
  })
  return (
    <div>
      <RadarChart data={radarData} title="综合评价总分" />
      <Tabs defaultActiveKey="0">
        {chartTabPanes}
      </Tabs>
    </div>
  )
}

export default ChartGraph;