import React from 'react';
import { Descriptions,Tabs } from 'antd';

import { AppContext } from '../../context';


const { useContext } = React;
const { Item } = Descriptions;
const { TabPane } = Tabs;

const SubjectIndex = ()=>{
  const { journalInfo } = useContext(AppContext);
  const isEmpty = journalInfo.length ===0;
  if(isEmpty) return <span/>
  const subjectIndexArr = journalInfo[0].subject_index;
  const tabPanes = subjectIndexArr.map((d,index)=>{
    return (
      <TabPane tab={d.name} key={index+1}>
         <Descriptions bordered size="middle" column={4}>
          <Item label="综合评价总分">{d.score}</Item>
          <Item label="综合评价总分学科排名">{d.score_rank}</Item>
          <Item label="核心总被引频次学科排名">{d.frequency_rank}</Item>
          <Item label="核心影响因子学科排名">{d.factor_rank}</Item>
          <Item label="学科影响指标">{d.influence}</Item>
          <Item label="学科扩散指标">{d.spread}</Item>
          <Item label="红点指标">{d.red_dot}</Item>
         </Descriptions>
      </TabPane>
    )
    
  })
  return (
    <Tabs defaultActiveKey="1">
    {tabPanes}
    </Tabs>
   
  )
}

export default SubjectIndex