import React from 'react';
import { PageHeader,Row,Descriptions,Col,Image,Tag,Tooltip } from 'antd';
import config from '../../config';
import { AppContext } from '../../context';
import { useHistory } from 'react-router-dom';

const { coverImage } = config;
const { useContext } = React;

const ProfileJournal = ()=>{
  const { journalInfo } = useContext(AppContext);
  const history = useHistory();
  const isEmpty = journalInfo.length ===0;
  if(isEmpty) return <span/>

  const journalProfileInfo = journalInfo[0].journal;
  const handleClick = sid=>{
    history.push('/subject/'+sid);
  }
  const subject =  journalInfo[0].subject_index.map(d=>{
    return (
      <Tooltip title="点击跳转到学科页面" placement="bottom" color="blue" key={d.subject_id}>
        <Tag  color="blue" onClick={()=>handleClick(d.subject_id)} style={{cursor:'pointer'}}>{d.name}</Tag>
      </Tooltip>
      
      )
  });

  const Profile = ({ children,extraContent }) => {
    return (
      <Row gutter={16} justify="space-between" align="middle">
        <Col className="gutter-row image" span={4}>{extraContent}</Col>
        <Col className="gutter-row" span={20}>{children}</Col>
      </Row>
    );
  };

  const img = (
      <Image
      src={coverImage + journalProfileInfo.img_name + ".jpg"}
      fallback="data:image/png;base64,"   
      alt="期刊封面"
      width="50%"
      /> 
  )

  const content = (
    <Descriptions size="small" column={3}>
      <Descriptions.Item label="类别">{journalProfileInfo.category}</Descriptions.Item>
      <Descriptions.Item label="ISSN">{journalProfileInfo.issn}</Descriptions.Item>
      <Descriptions.Item label="CN">{journalProfileInfo.cn}</Descriptions.Item>
      <Descriptions.Item label="学科">{subject}</Descriptions.Item>
      <Descriptions.Item label="出版周期">{journalProfileInfo.publishing_cycle}</Descriptions.Item>
      <Descriptions.Item label="创刊时间">{journalProfileInfo.publication_time}</Descriptions.Item>
      <Descriptions.Item label="主编">{journalProfileInfo.editor}</Descriptions.Item>
      <Descriptions.Item label="电话">{journalProfileInfo.telephone}</Descriptions.Item>
      <Descriptions.Item label="邮编">{journalProfileInfo.post_code}</Descriptions.Item>
      <Descriptions.Item label="Email">{journalProfileInfo.email}</Descriptions.Item>
      <Descriptions.Item label="地址">{journalProfileInfo.address}</Descriptions.Item>
      <Descriptions.Item label="主管单位">{journalProfileInfo.competent_unit}</Descriptions.Item>
      <Descriptions.Item label="主办单位">{journalProfileInfo.sponsor}</Descriptions.Item>
    </Descriptions>
  )

  return (
    <PageHeader
        ghost={false}
        onBack={() => window.history.back()}
        title={journalProfileInfo.name}
        subTitle={journalProfileInfo.english_name}
      >
        <Profile extraContent={img}>
          {content}
        </Profile>
    </PageHeader>  
  )

}

export default ProfileJournal;