import React from 'react';
import {
  WordCloud
} from '@ant-design/charts';


const getRandomColor = () => {
  const arr = [
    '#5B8FF9',
    '#5AD8A6',
    '#5D7092',
    '#F6BD16',
    '#E8684A',
    '#6DC8EC',
    '#9270CA',
    '#FF9D4D',
    '#269A99',
    '#FF99C3',
  ];
  return arr[Math.floor(Math.random() * (arr.length - 1))];
}

const WordCloudGraph = ({
  data
}) => {
  const config = {
    data: data,
    forceFit:true,
    wordStyle: {
      rotation: [-Math.PI / 2, Math.PI / 2],
      rotateRatio: 0.5,
      rotationSteps: 4,
      fontSize: [10, 60],
      color: (word, weight) => {
        return getRandomColor();
      },
      active: {
        shadowColor: '#666',
        shadowBlur: 8,
      },
      gridSize: 8,
    },
    shape: 'cardioid',
    shuffle: false,
    backgroundColor: '#fff',
    tooltip: {
      visible: true,
    },
    selected: -1
  }
  return (
      <WordCloud {...config} />
  );

}

export default WordCloudGraph;